import React, {Component} from 'react'
import Dropzone from 'react-dropzone'
import request from 'superagent'
import './Imageupload.css'
/*
Cloudinary API
*/
const CLOUDINARY_UPLOAD_PRESET = "vincent";
const CLOUDINARY_UPLOAD_URL = "https://api.cloudinary.com/v1_1/vincentkoo/image/upload"
var style = {
	cursor: "pointer",
	width: "100%",
	height:"100%",
}
var dragstyle = {
	width:"150px",
	height:"130px",
	border:"2px dashed rgb(102,102,102)",
	borderRadius:"5px"
}

class ContactForm extends Component{
	constructor(props) {
	    super(props);
	    this.state = {
	      uploadedFileCloudinaryUrl: ''
	    };
	  }
	  /*
		react-dropzone  onImageDrop  files 
	  */
	  onImageDrop(files) {
	    this.setState({
	      uploadedFile: files[0]
	    });
	 
	    this.handleImageUpload(files[0]);
	  }
	  disappera() {
	  	let drag = this.refs.drag;
	  	drag.style.display = "none";
	  }
	  /*
		 superagent  const POST  Cloudinary 
		.field   POST  Cloudinary  .end
	  */
	  handleImageUpload(file) {
	    let upload = request.post(CLOUDINARY_UPLOAD_URL)
	                        .field('upload_preset', CLOUDINARY_UPLOAD_PRESET)
	                        .field('file', file);
	 
	    upload.end((err, response) => {
	      if (err) {
	        console.error(err);
	      }
	 
	      if (response.body.secure_url !== '') {
	        this.setState({
	          uploadedFileCloudinaryUrl: response.body.secure_url
	        });
	        this.disappera();
	      }
	    });
	  }
	  /*
		

	  */
	render() {
		return(
				<div id="imageUploadWrapper">
			      <div ref="drag" className="FileUpload">
			        <Dropzone  style={dragstyle}
				      multiple={false}
				      accept="image/*"
				      onDrop={this.onImageDrop.bind(this)}>
				      <div style = {style}></div>
				    </Dropzone>
			      </div>
			 
			      <div>
			        {this.state.uploadedFileCloudinaryUrl === '' ? null :
			        <div id="innerImage">
			          <img id="img" alt="" src={this.state.uploadedFileCloudinaryUrl} />
			        </div>}
			      </div>
			    </div>
		)
	}
} 

export default ContactForm


