import React, {Component} from 'react'
import ReactDom from 'react-dom'
import MySelect from './Selete.react';
import './SearchPannel.css';

var arrow = require('../img/arrow-down.png');
var filterKey = {
    plat: "all",
    state: "all",
    daysPre: "1",
    daysAfter: "7",
},
    domSets = {};
class SearchPannel extends Component {
    constructor(){
        super(); 
        this.state = {
            value : []    //valuetags  selectstate
        } 
    }
    changeValue(value) {
        this.setState({
            value: value
        },function () { filterKey.select = this.state.value;})    /*setstate*/
    }
    componentDidMount() {
        domSets.terminiDom = ReactDom.findDOMNode(this.refs.termini);
        domSets.daysPreDom = ReactDom.findDOMNode(this.refs.daysPre);
        domSets.daysAfterDom = ReactDom.findDOMNode(this.refs.daysAfter);
        domSets.stateDom = ReactDom.findDOMNode(this.refs.state);
        domSets.platDom = ReactDom.findDOMNode(this.refs.plat);
        domSets.idDom = ReactDom.findDOMNode(this.refs.id);
        domSets.keyWordDom = ReactDom.findDOMNode(this.refs.keyWord);
    }
    Tchange() {  
        filterKey.termini = domSets.terminiDom.value;
    }
    dPchange() {
        filterKey.daysPre = domSets.daysPreDom.value;
    }
    dAchange() {
        filterKey.daysAfter = domSets.daysAfterDom.value;
    }
    Schange() {
        filterKey.state = domSets.stateDom.value
    }
    Plchange() {
        filterKey.plat = domSets.platDom.value
    }
    IDchange() {
        filterKey.id = domSets.idDom.value
    }
    Kchange() {
        filterKey.keyWord = domSets.keyWordDom.value
    }
    reset() {
        filterKey = {
            plat: "all",
            state: "all",
            daysPre: "1",
            daysAfter: "7",
        }
        domSets.keyWordDom.value = "";
        domSets.terminiDom.value = "";
        domSets.daysPreDom.value = "";
        domSets.daysAfterDom.value = "";
        domSets.stateDom.value = "all";
        domSets.platDom.value = "all";
        domSets.idDom.value = "";
        this.props.searchItems(filterKey);   //
        this.setState({
            value : []            //selecttags
        })  
    }

    queryhandle() {
        this.props.searchItems(filterKey);
        
    }
    render() {
        return (
            <div className="serachPannel">
                <span className="active"><strong></strong></span> / <span class="normal"></span>
                <table id="searchTable" cellPadding="10">
                    <tr>
                        <td>
                            <span></span><input onChange={this.Tchange.bind(this)} ref="termini" className="left" type="text" placeholder=""/>
                        </td>
                        <td>
                            <span></span><input onChange={this.dPchange.bind(this)} ref="daysPre" className="days" type="number" min="1" max="7" placeholder="1"/>  <input onChange={this.dAchange.bind(this)} ref="daysAfter" className="days" type="number" min="1" max="7" placeholder="7"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <span></span> 
                            <div className="myselect">
                                <MySelect changeValue={this.changeValue.bind(this)} value={this.state.value}/>
                            </div>
                             
                        </td>
                        <td>
                            <span></span>
                            <div className="selectWrapper1">
                                <select onChange={this.Schange.bind(this)} ref="state" name="state" id="select1">
                                    <option value="all"></option>
                                    <option value="published"></option>
                                    <option value="unpublished"></option>
                                </select>
                                <img alt="" src={arrow} />
                            </div>
                            
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <span></span>
                            <div className="selectWrapper2">
                                <select onChange={this.Plchange.bind(this)} ref="plat" name="plat" id="select2">
                                    <option value="all"></option>
                                    <option value="meituan"></option>
                                </select>
                                <img alt="" src={arrow} />
                            </div>

                        </td>
                        <td>
                            <span>id</span><input onChange={this.IDchange.bind(this)} ref="id" className="right" type="text" placeholder="id"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <span></span><input onChange={this.Kchange.bind(this)} ref="keyWord" className="left" type="text" placeholder=""/>
                        </td>
                    </tr>
                </table>
                <ul class="buttonManage">
                    <li>
                        <input type="button" id="query" value="" onClick={this.queryhandle.bind(this)}/>
                        <input onClick={this.reset.bind(this)} type="button" id="reset" value=""/>
                    </li>
                    <li>
                        <input type="button" className="batch" id="online" value="" />
                        <input type="button" className="batch" id="offline" value="" />
                    </li>
                </ul>
            </div>
        )
    }
}

export default SearchPannel


